@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Nunito', sans-serif;
}

body{
    background-color: #e2dada;
}

.navbar {
    display: flex;
    justify-content: space-between;
    background-color: #370075;
    color: #fdba31;
    padding: 1em;
}

.logo{
    display: flex;
    align-items: center;
    padding: 0 1em;
}
.nav{
    display: flex;
    align-items: center;
    padding: 0 1em;
}

.logo h1, .nav__a{
    text-decoration: none;
    color: #fdba31;
    margin: 10px;
}
.moe{
    height: 3.5em;
    width: 3.5em;
}
.CarritoBirra {
    width: 4em;
    height: 4em;
}

.cerveCarrito{
    display: flex;
}

.cerveCarrito p{
    text-align: center;
    font-size: 20px;
    color: #370075;
    font-weight: 700;
}

.cerveCarritoFondo{
    background-color: #fdba31;
    border-radius: 40px;
    height: 26px;
    width: 30px;
}

.cerveCarritoOpen{
    position: absolute;
    right: 0px;
    background-color: #370075;
    top: 95px;
    display: flex;
    flex-direction: column;
}

.cerveCarritoOpen button{
    display: block;
    width: 50%;
    color: #370075;
    background-color: #fdba31;
    border: none;
    margin: 10px auto;
    padding: 10px;
    font-weight: 600;
    font-size: 15px;
}


.itemListC__titulo{
    margin-top: 30px;
    text-align: center;
    text-decoration: underline;
}
.item__detalle a{
    color: #370075;
    text-decoration: none;
    font-weight: 700;
    vertical-align: sub;
    text-transform: uppercase;
}
.item__detalle{
    margin-top: 15px;
    border: 2px solid #370075;
    border-radius: 40px;
    height: 40px;
}

.item__detalle:hover{
    background-color: #c295f5;
}

.price{
    font-weight: 700;
    font-size: 22px;
    margin: 20px;
}

hr{
    height: 2px;
    border: 0px solid #fdba31;
    background-color: #fdba31;
    color:#fdba31;
    margin: 10px;
}
.itemList{
    border: 2px solid #fdba31;
    box-shadow: 2px 2px 2px 1px #37007536;

    flex-direction: column;
    align-items: center;
    padding: 10px;
    margin: 20px;
    width: 300px;
    text-align: center;
}

.itemList img{
    height: 150px;
}

.item__titulo{
        text-align: center;
        color: #370075;
        padding: 1em;
}

.bloque{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.footer {
    display: flex;
    justify-content: flex-end;
    background-color: #370075;
    color: #fdba31;
    padding: 1.5em;
}
.cartDetail{
    border: 2px solid #fdba31;
    box-shadow: 2px 2px 2px 1px #37007536;
    padding: 10px;
    margin: 10px;
    width: 300px;
    text-align: center;
}

.cartDetail button{
    width: 200px;
    color: #370075;
    background-color: #fdba31;
    border: none;
    padding: 5px;
    margin: 14px auto;
    font-weight: 600;
    font-size: 20px;
}

.carrito{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
}

.carrito h1{
    text-align: center;
}

.carrito_vacio{
    display: flex;
    width: 200px;
    justify-content: center;
    align-items: center;
    height: 50px;
    color: #370075;
    background-color: #fdba31;
    border: none;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 20px;
    border-radius: 20px;
}

.carritoContenedor{
    text-align: center;
    display: flex;
    width: 80%;
    justify-content: space-evenly;
}

.carritoVacioMensaje{
    font-size: 30px;
    margin: 20px;
}

.carritoVacioBoton{
    display: block;
    color: #370075;
    background-color: #fdba31;
    border: none;
    margin: 10px auto;
    font-weight: 600;
    font-size: 20px;
    text-decoration: none;
    text-align: center;
    padding: 10px;
    border-radius: 20px;
}

.carrito_final{
    display: flex;
    width: 200px;
    justify-content: center;
    height: 50px;
    align-items: center;
    color: #370075;
    background-color: #fdba31;
    border: none;
    margin: 10px auto;
    font-weight: 600;
    font-size: 20px;
    text-decoration: none;
    text-align: center;
    border-radius: 20px;
}

.total__botones{
    margin: 10px;
    font-size: 20px;
}
.itemDetail{
    border: 2px solid #fdba31;
    box-shadow: 2px 2px 2px 1px #37007536;
    display: flex;
    padding: 10px;
    margin: 20px auto;
    width: 800px;
}

.itemDetails{
    width: 300px;
    text-align: center;
}
.itemDetail__img{
    max-height: 400px;
}

.agregar{
    display: block;
    width: 100%;
    color: #370075;
    background-color: #fdba31;
    border: none;
    padding: 10px;
    font-weight: 600;
    font-size: 20px;
}

.itemDetail__precio{
    font-size: 30px;
    font-weight: 500;
}

.itemDetail__serial{
    font-size: 15px;
}

.itemDetail a{
    display: flex;
    justify-content: center;
    width: 50%;
    color: #370075;
    
    border: 2px solid #fdba31 ;
    border-radius: 20px;
    margin: 5px;
    padding: 10px;
    font-weight: 600;
    font-size: 20px;
}
.itemDetail a:hover{
    background-color: #fdba31;
}

.texto{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.producto{
    display: inline-block;
    margin: 20px;
    padding: 10px;
    text-align: center;
}

.Corona{
    width: 200px;
}

.botonAccion{
    width: 40px;    
    height: 40px;
    color: #370075;
    background-color: #fdba31;
    border-radius: 40px;
    border: none;
    margin: 10px;
    padding: 10px;
}

.agregar{
    display: block;
    width: 100%;
    color: #370075;
    background-color: #fdba31;
    border: none;
    padding: 10px;
}
.container{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.formCheckout{
    margin: 10px;
    width: 500px;
    font-size: 20px;
}

.formCheckout input{
    width: 100%;
    height: 30px;
}

.formCheckout input:active{
    background-color: #fdba31 ;
}

.formCheckout button{
    color: #370075;
    background-color: #fdba31;
    padding: 14px 25px;
    border: none;
    margin: 10px auto;
    font-weight: 600;
    font-size: 18px;
    display: block;
    border-radius: 20px;
}

.compraRealizada{
    text-align: center;
    margin: 50px;
}

.moeHappy{
    margin: 10px;
}
.moeBar{
    width: 100%;
}
